section.main {
    width: 100vw;
    height: 100vh;
    color: #FDFDFD;
    display: flex;
    justify-content: center;
    align-items: center;
    align-content: center;
    flex-wrap: wrap;
}

div.container {
    width: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    align-content: center;
    flex-wrap: wrap;
}

img.imgUmg {
    width: 100px;
    height: auto;
    position: relative;
    margin: 0 0 30px 0;
}

h1 {
    width: 100%;
    text-align: center;
    font-size: 18pt;
}

button.buttonStart {
    font-size: 12pt;
    padding: .7rem 3rem;
    cursor: pointer;
    margin-top: 30px;
    background: var(--third);
    border-radius: 4px;
    transition: .1s;
    transform: scale(1);
    &:active {
        transform: scale(.9);
    }
    &:hover {
        opacity: .8;
    }
}