
h1.title {
    margin-top: 5vh;
    color: var(--third);
    font-size: 24pt;
}

div.mainDiv {
    width: 100vw;
    height: auto;
    display: flex;
    justify-content: center;
    align-items: start;
    align-content: start;
    flex-wrap: wrap;
    padding: 5vh 0 30vh 0;
}

div.headTools {
    width: 90%;
    padding: 1rem;
    display: flex;
    position: relative;
    justify-content: center;
    align-items: center;
    align-content: center;
    flex-wrap: wrap;
}

ul {
    display: flex;
    position: relative;
    justify-content: center;
    align-items: center;
    align-content: center;
    flex-wrap: wrap;
    list-style: none;
    margin-top: 10vh;
}

li.listBook {
    width: 300px;
    margin: 2rem;
    padding: 1rem;
    border-radius: 4px;
    background: #0f0f0f;
    border: 1px dotted #222;
    box-shadow: -5px 1px 10px rgba(0,0,0,.6);
    text-align: left;
    display: flex;
    justify-content: center;
    align-items: center;
    align-content: center;
    flex-wrap: wrap;
}

li.requested {
    opacity: .2;
}

ul li p {
    width: 90%;
    padding: 2px 0;
}

ul li strong {
    color: var(--third);
}

img.bookImg {
    width: 150px;
    filter: invert(80%);
    margin-bottom: 2rem;
}

div.contActions {
    width: 90%;
    padding: 4px;
    display: flex;
    justify-content: center;
    align-items: center;
    align-content: center;
    flex-wrap: nowrap;
    margin-top: 1rem;
}

div.contActions button {
    border-radius: 4px;
    background: var(--third);
    padding: .5rem 1rem;
    transition: .1s;
    transform: scale(1);
    cursor: pointer;
    font-size: 11pt;
    &:active {
        transform: scale(.9);
    }
}




div.contSearch {
    width: 100%;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    align-content: center;
}

div.contSearch input {
    font-size: 12pt;
    border-radius: 4px;
    padding: 1rem 3rem;
    border-radius: 200px;
    color: #212121;
    font-weight: bold;
    width: 500px;
    border: 1px solid var(--third);
    background: transparent;
    color: var(--forth);
}

button.btnAdd {
    padding: .5rem 1rem;
    background: var(--third);
    border: none;
    color: var(--primary);
    cursor: pointer;
    font-size: 11pt;
    border-radius: 4px;
    margin-top: 1rem;
    border-bottom: 1px solid var(--third);
    transition: .1s;
    transform: scale(1);
    &:active {
        transform: scale(.9);
    }
}




.modal {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
  }
  
  .modalContent {
    background: var(--primary);
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
    width: 400px;
    max-width: 100%;
  }
  
  .modal h2 {
    margin: 0 0 20px;
    color: var(--third);
  }
  
  .modal label {
    display: block;
    margin-bottom: 4px;
    margin-top: 1rem;
  }
  
  .modal input {
    width: 100%;
    padding: .5rem 1rem;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
    font-weight: bold;
    font-size: 11pt;
  }
  
div.contBtnModal {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    align-content: center;
    width: 100%;
}

  .modal button {
    margin-top: 10px;
    padding: 10px 15px;
    background: var(--third);
    color: var(--primary);
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: .1s;
    transform: scale(1);
    &:active {
        transform: scale(.9);
    }
  }
