:root {
    --primary: #0a0a0a;
    --secondary: #363636;
    --third: #00c896;
    --forth: #f2ecff;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    outline: none;
    font-family: sans-serif;
    border: none;
}

body {
    width: 100vw;
    height: 100vh;
    position: relative;
    overflow-y: auto;
    overflow-x: hidden;
    background: var(--primary);
    color: var(--forth);
}